// App.vue
<template>
  <div class="container">
    <div class="image-converter">
      <div class="upload-section">
        <input type="file" accept="image/*" @change="onFileSelected" ref="fileInput">
        <button @click="$refs.fileInput.click()">Select Image</button>
      </div>
      
      <div v-if="imageLoaded" class="preview-section">
        <h3>Preview</h3>
        <div class="image-preview">
          <div class="preview-container">
            <img class="checkerboard" :src="imagePreview" alt="미리보기" :style="{ objectFit: 'contain' }" />
            <div class="image-info">
              {{ imageWidth }} X {{ imageHeight }}
            </div>
          </div>
          
        </div>
        
        <div class="conversion-options">
          <h3>Conversion Options</h3>
          <div class="format-selection">
            <div class="option-row">
              <div class="option-label">
                <h3>Color Format</h3>
              </div>
              <div class="option-control">
                <div class="select-wrapper">
                  <select v-model="format">
                    <optgroup label="32-bit Format (8888)">
                      <option value="rgba8888">RGBA8888</option>
                      <option value="argb8888">ARGB8888</option>
                      <option value="bgra8888">BGRA8888</option>
                      <option value="abgr8888">ABGR8888</option>
                    </optgroup>
                    <optgroup label="16-bit Format (4444)">
                      <option value="rgba4444">RGBA4444</option>
                      <option value="argb4444">ARGB4444</option>
                      <option value="bgra4444">BGRA4444</option>
                      <option value="abgr4444">ABGR4444</option>
                    </optgroup>
                    <optgroup label="16-bit Format (565)">
                      <option value="rgb565">RGB565</option>
                      <option value="bgr565">BGR565</option>
                    </optgroup>
                  </select>
                </div>
                <div class="color-legend-item">
                  <div class="color-legend" :class="getLegendClass">
                    <template v-for="channel in getChannelOrder" :key="channel">
                      <span :style="getChannelStyle(channel)">{{ channel }}</span>
                    </template>
                  </div>
                  <span class="legend-description">{{ getFormatDescription }}</span>
                </div>
              </div>
            </div>
          </div>
          <div class="output-selection">
          <div class="option-row">
            <div class="option-label">
              <h3>Output Format</h3>
            </div>
            <div class="option-control">
              <div class="select-wrapper">
                <select v-model="outputType">
                  <option value="binary">Binary</option>
                  <option value="c-array">C Array</option>
                </select>
              </div>
            </div>
          </div>
        </div>
        </div>
        
        
        
        <button @click="convertImage">Convert</button>
      </div>
      
      <div v-if="convertedSize" class="conversion-info">
        <p>Converted File Size: {{ (convertedSize / 1024).toFixed(2) }} KB</p>
        <p>Compression Ratio: {{ getCompressionRatio() }}%</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      imageLoaded: false,
      imagePreview: '',
      imageData: null,
      imageWidth: 0,
      imageHeight: 0,
      format: 'rgba8888',
      outputType: 'binary',
      convertedSize: 0
    }
  },
  computed: {
    getLegendClass() {
      return this.format.split(/\d+/)[0];
    },
    getChannelOrder() {
      const format = this.format.toLowerCase();
      if (format.includes('565')) {
        return format.startsWith('rgb') ? ['R', 'G', 'B'] : ['B', 'G', 'R'];
      }
      return format.split(/\d+/)[0].toUpperCase().split('');
    },
    getFormatDescription() {
      if (this.format.includes('8888')) {
        return '32-bit (Each channel 8-bit)';
      } else if (this.format.includes('4444')) {
        return '16-bit (Each channel 4-bit)';
      } else {
        return '16-bit (R:5, G:6, B:5)';
      }
    }
  },
  methods: {
    onFileSelected(event) {
      const file = event.target.files[0];
      if (!file) return;
      
      const reader = new FileReader();
      reader.onload = (e) => {
        this.imagePreview = e.target.result;
        this.loadImageData(e.target.result);
      };
      reader.readAsDataURL(file);
    },
    
    loadImageData(src) {
      const img = new Image();
      img.onload = () => {
        this.imageWidth = img.width;
        this.imageHeight = img.height;
        
        const canvas = document.createElement('canvas');
        canvas.width = img.width;
        canvas.height = img.height;
        
        const ctx = canvas.getContext('2d');
        ctx.drawImage(img, 0, 0);
        
        this.imageData = ctx.getImageData(0, 0, img.width, img.height).data;
        this.imageLoaded = true;
      };
      img.src = src;
    },
    
    convertImage() {
      if (!this.imageData) return;
      
      let result;
      
      // 형식별 변환 처리
      if (this.format.endsWith('8888')) {
        result = this.convertTo8888Format(this.format);
      } else if (this.format.endsWith('4444')) {
        result = this.convertTo4444Format(this.format);
      } else if (this.format.endsWith('565')) {
        result = this.convertTo565Format(this.format);
      }
      
      this.convertedSize = result.length;
      
      // 출력 형식에 따른 다운로드
      if (this.outputType === 'binary') {
        this.downloadBinary(result);
      } else {
        this.downloadCArray(result);
      }
    },
    
    convertTo8888Format(format) {
      const length = this.imageData.length / 4;
      const result = new Uint8Array(this.imageData.length);
      
      for (let i = 0; i < length; i++) {
        const r = this.imageData[i * 4];     // Red
        const g = this.imageData[i * 4 + 1]; // Green
        const b = this.imageData[i * 4 + 2]; // Blue
        const a = this.imageData[i * 4 + 3]; // Alpha
        
        const pixelOffset = i * 4;
        
        // 포맷별 바이트 순서 배치
        switch (format) {
          case 'rgba8888':
            result[pixelOffset] = r;
            result[pixelOffset + 1] = g;
            result[pixelOffset + 2] = b;
            result[pixelOffset + 3] = a;
            break;
          case 'argb8888':
            result[pixelOffset] = a;
            result[pixelOffset + 1] = r;
            result[pixelOffset + 2] = g;
            result[pixelOffset + 3] = b;
            break;
          case 'bgra8888':
            result[pixelOffset] = b;
            result[pixelOffset + 1] = g;
            result[pixelOffset + 2] = r;
            result[pixelOffset + 3] = a;
            break;
          case 'abgr8888':
            result[pixelOffset] = a;
            result[pixelOffset + 1] = b;
            result[pixelOffset + 2] = g;
            result[pixelOffset + 3] = r;
            break;
        }
      }
      
      return result;
    },
    
    convertTo4444Format(format) {
      const length = this.imageData.length / 4;
      const result = new Uint8Array(length * 2); // 각 픽셀당 2바이트
      
      for (let i = 0; i < length; i++) {
        const r = Math.floor(this.imageData[i * 4] / 16);       // Red: 0-15
        const g = Math.floor(this.imageData[i * 4 + 1] / 16);   // Green: 0-15
        const b = Math.floor(this.imageData[i * 4 + 2] / 16);   // Blue: 0-15
        const a = Math.floor(this.imageData[i * 4 + 3] / 16);   // Alpha: 0-15
        
        const pixelOffset = i * 2;
        
        // 포맷별 4비트 값 패킹
        switch (format) {
          case 'rgba4444':
            result[pixelOffset] = (r << 4) | g;
            result[pixelOffset + 1] = (b << 4) | a;
            break;
          case 'argb4444':
            result[pixelOffset] = (a << 4) | r;
            result[pixelOffset + 1] = (g << 4) | b;
            break;
          case 'bgra4444':
            result[pixelOffset] = (b << 4) | g;
            result[pixelOffset + 1] = (r << 4) | a;
            break;
          case 'abgr4444':
            result[pixelOffset] = (a << 4) | b;
            result[pixelOffset + 1] = (g << 4) | r;
            break;
        }
      }
      
      return result;
    },
    
    convertTo565Format(format) {
      const length = this.imageData.length / 4;
      const result = new Uint8Array(length * 2); // 각 픽셀당 2바이트
      
      for (let i = 0; i < length; i++) {
        // 원본 0-255 값을 비트 수에 맞게 변환
        // R: 5비트 (0-31), G: 6비트 (0-63), B: 5비트 (0-31)
        const r = Math.floor(this.imageData[i * 4] / 255 * 31);      // Red: 0-31
        const g = Math.floor(this.imageData[i * 4 + 1] / 255 * 63);  // Green: 0-63
        const b = Math.floor(this.imageData[i * 4 + 2] / 255 * 31);  // Blue: 0-31
        
        const pixelOffset = i * 2;
        
        // RGB565는 16비트 색상 형식으로 다음과 같이 구성됩니다.
        // RRRRRGGGGGGBBBBB (R: 5비트, G: 6비트, B: 5비트)
        let pixel16bit;
        
        if (format === 'rgb565') {
          // RGB565: R(5비트) | G(6비트) | B(5비트)
          pixel16bit = (r << 11) | (g << 5) | b;
        } else { // BGR565
          // BGR565: B(5비트) | G(6비트) | R(5비트)
          pixel16bit = (b << 11) | (g << 5) | r;
        }
        
        // 16비트 값을 2개의 바이트로 저장 (리틀 엔디안)
        result[pixelOffset] = pixel16bit & 0xFF;         // 하위 8비트
        result[pixelOffset + 1] = (pixel16bit >> 8) & 0xFF; // 상위 8비트
      }
      
      return result;
    },
    
    downloadBinary(data) {
      const blob = new Blob([data], { type: 'application/octet-stream' });
      const url = URL.createObjectURL(blob);
      const filename = `image_${this.format}_${this.imageWidth}x${this.imageHeight}.bin`;
      
      this.triggerDownload(url, filename);
    },
    
    downloadCArray(data) {
      let cCode = `// ${this.format} 이미지 데이터 - ${this.imageWidth}x${this.imageHeight}\n`;
      
      // 각 형식에 대한 설명 추가
      if (this.format.includes('rgba')) {
        cCode += '// Byte order: Red, Green, Blue, Alpha\n';
      } else if (this.format.includes('argb')) {
        cCode += '// Byte order: Alpha, Red, Green, Blue\n';
      } else if (this.format.includes('bgra')) {
        cCode += '// Byte order: Blue, Green, Red, Alpha\n';
      } else if (this.format.includes('abgr')) {
        cCode += '// Byte order: Alpha, Blue, Green, Red\n';
      } else if (this.format === 'rgb565') {
        cCode += '// RGB565 Format: 16-bit (RRRRRGGGGGGBBBBB)\n';
        cCode += '// Little-endian byte order: Lower 8-bit, Upper 8-bit\n';
      } else if (this.format === 'bgr565') {
        cCode += '// BGR565 Format: 16-bit (BBBBBGGGGGGRRRRR)\n';
        cCode += '// Little-endian byte order: Lower 8-bit, Upper 8-bit\n';
      }
      
      // 4444 형식인 경우 패킹 설명 추가
      if (this.format.includes('4444')) {
        cCode += '// 4-bit packing format: Each byte contains two 4-bit color values\n';
      }
      
      cCode += `const unsigned char imageData[${data.length}] = {\n`;
      
      const bytesPerLine = 12;
      for (let i = 0; i < data.length; i += bytesPerLine) {
        cCode += '    ';
        for (let j = 0; j < bytesPerLine && i + j < data.length; j++) {
          cCode += `0x${data[i + j].toString(16).padStart(2, '0')}`;
          if (i + j < data.length - 1) {
            cCode += ', ';
          }
        }
        cCode += '\n';
      }
      
      cCode += '};\n\n';
      cCode += `const int imageWidth = ${this.imageWidth};\n`;
      cCode += `const int imageHeight = ${this.imageHeight};\n`;
      cCode += `const char* format = "${this.format}";\n`;
      
      // 565 형식인 경우 샘플 코드 추가
      if (this.format.includes('565')) {
        cCode += `\n// Example function to extract 16-bit pixel values for ${this.format} format\n`;
        cCode += `uint16_t getPixel(int x, int y) {\n`;
        cCode += `    if (x < 0 || x >= imageWidth || y < 0 || y >= imageHeight) return 0;\n`;
        cCode += `    int offset = (y * imageWidth + x) * 2; // Each pixel is 2 bytes\n`;
        cCode += `    return (uint16_t)(imageData[offset] | (imageData[offset + 1] << 8));\n`;
        cCode += `}\n\n`;
        
        cCode += `// Extract color components from 16-bit ${this.format} pixels\n`;
        if (this.format === 'rgb565') {
          cCode += `void getRGBComponents(uint16_t pixel, uint8_t* r, uint8_t* g, uint8_t* b) {\n`;
          cCode += `    *r = ((pixel >> 11) & 0x1F) * 255 / 31; // 5-bit Red component\n`;
          cCode += `    *g = ((pixel >> 5) & 0x3F) * 255 / 63;  // 6-bit Green component\n`;
          cCode += `    *b = (pixel & 0x1F) * 255 / 31;         // 5-bit Blue component\n`;
          cCode += `}\n`;
        } else { // BGR565
          cCode += `void getBGRComponents(uint16_t pixel, uint8_t* b, uint8_t* g, uint8_t* r) {\n`;
          cCode += `    *b = ((pixel >> 11) & 0x1F) * 255 / 31; // 5-bit Blue component\n`;
          cCode += `    *g = ((pixel >> 5) & 0x3F) * 255 / 63;  // 6-bit Green component\n`;
          cCode += `    *r = (pixel & 0x1F) * 255 / 31;         // 5-bit Red component\n`;
          cCode += `}\n`;
        }
      }
      
      const blob = new Blob([cCode], { type: 'text/plain' });
      const url = URL.createObjectURL(blob);
      const filename = `image_${this.format}_${this.imageWidth}x${this.imageHeight}.c`;
      
      this.triggerDownload(url, filename);
    },
    
    triggerDownload(url, filename) {
      const a = document.createElement('a');
      a.href = url;
      a.download = filename;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);
    },
    
    getCompressionRatio() {
      if (!this.convertedSize || !this.imageData) return '0';
      const originalSize = this.imageData.length;
      return ((this.convertedSize / originalSize) * 100).toFixed(1);
    },
    getChannelStyle(channel) {
      const styles = {
        'R': { background: '#ff0000' },
        'G': { background: '#00ff00' },
        'B': { background: '#0000ff' },
        'A': { 
          background: '#ffffff',
          color: '#000',
          textShadow: 'none',
          borderLeft: '1px solid #ddd',
          borderRight: '1px solid #ddd'
        }
      };
      return styles[channel];
    }
  }
}
</script>

